<template>
    <div>
        <form @submit="addTodoItem">
            <input type="text" v-model="title" name="title" placeholder="Add todo">
            <input type="submit" value="Submit" class="btn">
        </form>
    </div>
</template>
<script>
import { v4 as uuidv4 } from 'uuid';
export default {
    name: "AddTodoItem",
    data() {
        return {
            title : ''
        }
    },
    methods: {
        addTodoItem(e) {
            e.preventDefault();
            const newTodo = {
                id: uuidv4(),
                title: this.title,
                description: "Hey!!!",
                completed: false
            }
            console.log(newTodo);
            this.$emit('add-todo',newTodo);
            this.title = '';
        }
    }
}
</script>
<style scoped>
    form {
        display: flex;
    }
    input[text="text"] {
        flex: 10;
        padding: 10px;
    }
</style>